<script>
import api from "@/scripts/api"
import store from "@/scripts/store"
import {
  ValidateIP,
  ValidateMAC,
  ValidateDigits,
  FormatDate,
  RequestInfo
} from "@/scripts/utils"

export default {
  components: {
    // AT: () => import("@/components/CascaderAT"),
    Nat: () => import("@/components/SelectNat"),
    Supp: () => import("@/components/SelectSupp"),
    Meta: () => import("@/components/SelectMeta"),
