    },
    SyncLoading: {
      type: Boolean,
      default: false
    },
    JcName: {
      type: Boolean,
      default: false
    },
    DialogVisible: {
      type: Boolean,
      default: false
    },
    DeviceType: {
      type: Number,
      default: -1
    },
    ActiveTab: {
      type: String,
      default: "all"
    },
    Devicedatas: {
      type: Object,
      default: null
    },
    Dcxinxi: {
      type: Object,
      default: null
    },
    Drlist: {
      type: Array,
      default: () => []
    },
    RecordBoolean: {
      type: Boolean,
      default: false
    },
    ParamBoolean: {
      type: Boolean,
      default: false
    },
    // 是否需要遮蔽层
    isModal: {
      type: Boolean,
      default: true
    }
  },
