      this.jcname = newVal
    },
    DeviceType (newVal, oldVal) {
      this.number = newVal
      this.PageRules()
    },
    ParamBoolean (newVal, oldVal) {
      this.paramBoolean = newVal
    },
    Drlist (newVal, oldVal) {
      this.DRList = []
      newVal.forEach((element) => {
        var result = ""
        switch (element.Key.DOType) {
          case 10:
            result = "入库"
            break
          case 20:
            result = "领用"
            break
          case 30:
            result = "退库"
            break
          case 40:
            result = "借用"
            break
          case 50:
            result = "归还"
            break
          case 60:
            result = "调拨申请"
            break
          case 61:
            result = "确认调拨"
            break
          case 62:
            result = "取消调拨"
            break
          case 70:
            result = "报修"
            break
          case 71:
            result = "维修"
            break
          case 73:
            result = "维修完成"
            break
          case 80:
            result = "报废申请"
            break
          case 81:
            result = "报废"
            break
          case 82:
            result = "取消报废"
            break
          case 90:
            result = "更换"
            break
          default:
            result = "无"
            break
        }
        var demo = element.Key.DODesc
        var drcontent = ""
        if (demo && demo !== "[]") {
          drcontent = JSON.parse(demo).dodesc
        }
        let date = "";
        element.Key.DODate ? date = FormatDate(element.Key.DODate, "yyyy-MM-dd") : date = "";
        this.DRList.push({
          UserName: element.UserName ? element.UserName : "系统",
          DOType: result,
          DODate: date,
          DODesc: drcontent
        })
      })
    },
    RecordBoolean (newVal, oldVal) {
      this.recordBoolean = newVal
    },
    Dcxinxi (newVal, oldVal) {
      this.BaseForm.entype = newVal.Key.DeviceType
      this.BaseForm.pid = newVal.Key.DeviceModel
      this.BaseForm.devicepic = newVal.Key.UserID
      this.BaseForm.devicecode = newVal.Key.DeviceCode
      this.BaseForm.deviceseat = newVal.Key.DeviceLocale
      this.BaseForm.ExpiryDate =
        newVal.Key.DeviceExpiryDate == null
          ? ""
          : FormatDate(newVal.Key.DeviceExpiryDate, "yyyy-MM-dd")
