          newVal.Key.DeviceAcquiredDate == null
            ? ""
            : FormatDate(newVal.Key.DeviceAcquiredDate, "yyyy-MM-dd")
        this.BaseForm.Precious = newVal.Key.DevicePrecious
        this.BaseForm.EnName = newVal.Key.DeviceEnName
        this.BaseForm.StatusCode = newVal.Key.DeviceStatusCode
        this.BaseForm.UseCode = newVal.Key.DeviceUseCode
        this.BaseForm.TypeCode = newVal.Key.DeviceTypeCode
        this.BaseForm.Supplier = newVal.Key.SupplierID
        this.BaseForm.XXDWCCM = newVal.Key.DeviceXXDWCCM
        this.BaseForm.PropertyCode = newVal.Key.DevicePropertyCode
        this.BaseForm.Use = newVal.Key.DeviceUse
        this.BaseForm.AreaCode = newVal.Key.DeviceAreaCode
        this.BaseForm.JFKMM = newVal.Key.DeviceJFKMM
        this.BaseForm.Source = newVal.Key.DeviceSource
        this.BaseForm.Manufacturer = newVal.Key.DeviceManufacturer
        this.BaseForm.Currency = newVal.Key.DeviceCurrency
        this.BaseForm.Prince = newVal.Key.DevicePrince
        this.BaseForm.FinancialAccount = newVal.Key.DeviceFinancialAccount
        this.BaseForm.Invoices = newVal.Key.DeviceInvoices
        this.BaseForm.Qualification = newVal.Key.DeviceQualification
        this.BaseForm.Config = newVal.Key.DeviceConfig
        this.BaseForm.Illustrate = newVal.Key.DeviceIllustrate
        this.BaseForm.Desc = newVal.Key.DeviceDescription
      }
      // 终端设备
      if (newVal.DeviceType === 1) {
        var a = newVal.ModelCode
        var b = newVal.DeviceSerialNo
        var c = b.substring(a.length)
        this.deviceSerialNo = a
        this.BaseForm.id = newVal.DeviceID
        this.BaseForm.DeviceName = newVal.DeviceName
        this.BaseForm.name = c
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.zdIP = newVal.ControlIP
        this.BaseForm.Port = newVal.ControlPort
        this.BaseForm.MAC = newVal.ControlMAC
        this.BaseForm.Gateway = newVal.ControlGateway
        this.BaseForm.SubNetMask = newVal.ControlSubNetMask
        this.BaseForm.Ver = newVal.ControlVer
      } // 摄像头
      if (newVal.DeviceType === 4) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Name
        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.sxtIP = newVal.Key.CameraIP
        this.BaseForm.sxtPort = newVal.Key.CameraPort
        this.BaseForm.sxtChannel = newVal.Key.CameraChannel
        this.BaseForm.sxtCameraUser = newVal.Key.CameraUser
        this.BaseForm.sxtPwd = newVal.Key.CameraPwd
        this.BaseForm.sxtSite = newVal.Key.CameraSite
      } // 课件编码器
      if (newVal.DeviceType === 5) {
