        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.kjbmqIP = newVal.Key.CameraIP
