        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.dhglIP = newVal.Key.IPPhoneIP
        this.BaseForm.dhglPort = newVal.Key.IPPhonePort
        this.BaseForm.dhglphone = newVal.Key.IPPhoneNo
        this.BaseForm.dhglCameraUser = newVal.Key.IPPhoneUser
        this.BaseForm.dhglPwd = newVal.Key.IPPhonePwd
      } // PC
      if (newVal.DeviceType === 2) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Name
        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.PCIP = newVal.Key.PCIP
        this.BaseForm.PCShutdownPort = newVal.Key.PCShutdownPort
        this.BaseForm.PCRemortUser = newVal.Key.PCRemortUser
        this.BaseForm.PCRemortPwd = newVal.Key.PCRemortPwd
        this.BaseForm.PCRemortPort = newVal.Key.PCRemortPort
      } // IC卡读卡器
      if (newVal.DeviceType === 7) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Name
        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.ICdkqIP = newVal.Key.IP
        this.BaseForm.ICdkqPort = newVal.Key.Port
      } // 投影仪
      if (newVal.DeviceType === 11) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Name
        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.ProjectorSerialPort = newVal.Key.ProjectorSerialPort
        this.BaseForm.ProjectorLightHours = newVal.Key.ProjectorLightHours
        this.BaseForm.ProjectorLightAlarm = newVal.Key.ProjectorLightAlarm
      } // 物联网
      if (newVal.DeviceType === 8) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Key.DeviceName
        this.BaseForm.name = newVal.Key.DeviceSerialNo
        this.BaseForm.ModelID = newVal.Key.ModelID
        this.BaseForm.RoomID = newVal.Key.RoomID
        this.BaseForm.iotIP = newVal.IP
        this.BaseForm.iotPort = newVal.Port
        this.BaseForm.iotChannel = newVal.Channel
        this.BaseForm.iotCharge = newVal.InitCharge
      } // 门禁主机
      if (newVal.DeviceType === 12) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Key.DeviceName
        this.BaseForm.name = newVal.Key.DeviceSerialNo
        this.BaseForm.ModelID = newVal.Key.ModelID
        this.BaseForm.RoomID = newVal.Key.RoomID
        this.BaseForm.egIP = newVal.IP
        this.BaseForm.egPort = newVal.Port
        this.BaseForm.egUser = newVal.User
        this.BaseForm.egPwd = newVal.Pwd
      } // LED
      if (newVal.DeviceType === 15) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Name
        this.BaseForm.name = newVal.deviceSerialNo
        this.BaseForm.ModelID = newVal.ModelID
        this.BaseForm.RoomID = newVal.ClsRoomID
        this.BaseForm.ledIP = newVal.Key.IP
        this.BaseForm.ledWidth = newVal.Key.Width
        this.BaseForm.ledHeight = newVal.Key.Height
      }
      if (newVal.DeviceType === 16) {
        this.BaseForm.id = newVal.Key.DeviceID
        this.BaseForm.DeviceName = newVal.Key.DeviceName
        this.BaseForm.name = newVal.Key.DeviceSerialNo
        this.BaseForm.ModelID = newVal.Key.ModelID
        this.BaseForm.RoomID = newVal.Key.RoomID
        this.BaseForm.gatewayIP = newVal.IP
        this.BaseForm.gatewayName = newVal.GatewayName
        this.BaseForm.gatewayType = parseInt(newVal.GatewayType)
        this.BaseForm.gfRTSP = newVal.FrameRTSP
        this.BaseForm.gcRTSP = newVal.CameraRTSP
      }
    }
  },
  data () {
    // 验证名称
    const ValidateName = async (rule, name, callback) => {
      if (!this.BaseForm.id) {
        const res = await api.Device.ValidateName(
          this.BaseForm.DeviceName,
          this.BaseForm.id
        )
        if (res.Result) {
          callback()
        } else {
          callback(new Error(res.Info))
        }
      }
    }
    // 验证终端IP
    const ValidateCtrlIP = async (rule, name, callback) => {
      const res = await api.Ctrl.ValidateIP(
        this.BaseForm.id,
        this.BaseForm.zdIP
      )
      if (res.Result) {
        callback()
      } else {
        callback(new Error(res.Info))
      }
    }
    // 验证LEDIP
    const ValidateLEDIP = async (rule, name, callback) => {
      const res = await api.LED.ValidateIP(
        this.BaseForm.ledIP,
        this.BaseForm.id
      )
      if (res.Result) {
        callback()
      } else {
        callback(new Error(res.Info))
      }
    }
    const ValidateBaseRequire = async (rule, name, callback) => {
      if (this.activeTab === "all") {
        if (name === "") {
          return callback(new Error("请选择日期"))
        }
      } else {
        callback()
      }
    }
    const pidBaseRequire = async (rule, name, callback) => {
      if (this.activeTab === "all") {
        if (name === "") {
          return callback(new Error("请选择资产类型"))
        }
      } else {
        callback()
      }
    }
    const $this = this
    return {
      deviceSerialNo: "",
      UserID: "",
      dlgVisible: this.DialogVisible,
      activeTab: this.ActiveTab,
      number: this.DeviceType,
      paramBoolean: this.ParamBoolean,
      recordBoolean: this.RecordBoolean,
      devicedatas: {},
      DRList: this.Drlist,
      loading: this.SyncLoading,
      Usedrugs: [
        {
          id: 1,
          name: "可用"
        },
        {
          id: 0,
          name: "不可用"
        }
      ],
      drugs: [
        {
          id: 1,
          name: "是精密贵重仪器"
        },
        {
          id: 0,
          name: "不是精密贵重仪器"
        }
      ],
      SourceOpts: [
        {
          value: "1",
          label: "盘盈"
        },
        {
          value: "2",
          label: "外购"
        },
        {
          value: "3",
          label: "调入"
        },
        {
          value: "4",
          label: "转入"
        },
        {
          value: "5",
          label: "捐赠"
        },
        {
          value: "6",
          label: "自制自建"
        },
        {
          value: "7",
          label: "其他"
        }
      ],
      siteOpts: [
        {
          value: 1,
          label: "拍学生"
        },
        {
          value: 2,
          label: "拍老师"
