          children: "options",
          expandTrigger: "hover",
          checkStrictly: true,
          async lazyLoad (node, resolve) {
            if (node.children) {
              if (node.children.length > 0) {
                node.children = []
              }
            }
            if (node.level > 0) {
              if (node.isDisabled === false) {
                const res = await api.AssetType.GetSubDatas(node.data.ATID)
                if (res && res.Datas && res.Datas.length > 0) {
                  res.Datas.forEach((element) => {
                    element.disabled = element.ATID === $this.BaseForm.id
                  })
                  resolve(res.Datas)
                }
              } else {
                resolve()
              }
            }
          }
        }
