      if (this.number !== -1) {
        var deverNo
        if (this.number === 1) {
          deverNo = this.deviceSerialNo + this.BaseForm.name
        } else {
          deverNo = this.BaseForm.name
        }
        var pRODDate1
        var acquiredDate1
        var expiryDate1
        if (
          this.BaseForm.PRODDate.length &&
          this.BaseForm.PRODDate.length === 10
        ) {
          pRODDate1 = this.BaseForm.PRODDate.substring(0, 10)
        } else {
          const month = this.BaseForm.PRODDate.getMonth() + 1
          pRODDate1 =
            this.BaseForm.PRODDate.getFullYear() +
            "-" +
            month +
            "-" +
            this.BaseForm.PRODDate.getDate()
        }
        if (
          this.BaseForm.AcquiredDate.length &&
          this.BaseForm.AcquiredDate.length === 10
        ) {
          acquiredDate1 = this.BaseForm.AcquiredDate.substring(0, 10)
        } else {
          const month = this.BaseForm.AcquiredDate.getMonth() + 1
          acquiredDate1 =
            this.BaseForm.AcquiredDate.getFullYear() +
            "-" +
            month +
            "-" +
            this.BaseForm.AcquiredDate.getDate()
        }
        if (
          this.BaseForm.ExpiryDate.length &&
          this.BaseForm.ExpiryDate.length === 10
        ) {
          expiryDate1 = this.BaseForm.ExpiryDate.substring(0, 10)
        } else {
          const month = this.BaseForm.ExpiryDate.getMonth() + 1
          expiryDate1 =
            this.BaseForm.ExpiryDate.getFullYear() +
            "-" +
            month +
            "-" +
            this.BaseForm.ExpiryDate.getDate()
        }
        const res = await api.Device.Save(
          this.BaseForm.id, // 设备编号
          this.BaseForm.ModelID, // 型号编号
          this.BaseForm.devicepic, // 设备负责人号
          this.BaseForm.RoomID, // 教室编号
          this.BaseForm.Supplier, // 供应商
          this.BaseForm.devicecode, // 设备编码
          this.BaseForm.DeviceName, // 仪器名称
          this.BaseForm.EnName, // 仪器英文名称
          this.BaseForm.entype, // 设备类型
          this.BaseForm.XXDWCCM, // 学校单位层次码
          this.BaseForm.PropertyCode, // 产权码
          this.BaseForm.UseCode, // 使用状况码
          this.BaseForm.TypeCode, // 分类码
          pRODDate1, // 出厂日期
          this.BaseForm.AreaCode, // 出厂地区
          this.BaseForm.JFKMM, // 经费科目码
          acquiredDate1, // 购置日期
          this.BaseForm.Manufacturer, // 生产厂家
          deverNo, // 出厂号/序列号
          this.BaseForm.Invoices, // 单据号
          this.BaseForm.deviceseat, // 仪器地点
          this.BaseForm.FinancialAccount, // 财务帐号
          this.BaseForm.Currency, // 价格币种
          this.BaseForm.Prince, // 仪器价格
          expiryDate1, // 保修截止日期
          this.BaseForm.Source, // 设备来源
          this.BaseForm.StatusCode, // 仪器现状码
          this.BaseForm.Precious, // 是否精密贵重仪器
          this.BaseForm.Use, // 使用状态 1 可用 0 不可用
          this.BaseForm.Qualification, // 技术指标
          this.BaseForm.Config, // 仪器配置
          this.BaseForm.Illustrate, // 仪器说明
          this.BaseForm.Desc, // 备注
          "", // 图片
          this.BaseForm.pid // 资产类型
        )
        if (res) {
          this.loading = false
          this.$emit("update:DialogVisible", false)
          this.$emit("load")
        }
        RequestInfo(res, '保存');
      }
    },
    /**
     * 加载基础数据
     */
    async LoadTreeData (level, pid, parent) {
      const res = await api.AssetType.GetSubDatas(pid)
      if (res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(async (element) => {
          element.disabled = element.ATID === this.BaseForm.id
        })
        this.$set(parent, "options", res.Datas)
      }
    },
    async ChangeModel (val) {
      if (val) {
        const res = await api.Model.GatData(val)
        if (res && res.Data) {
          this.deviceSerialNo = res.Data.ModelCode
          this.number = res.Data.DeviceType
          this.paramBoolean = true
          if (this.number === 9999) {
            this.paramBoolean = false
          }
          this.jcname = false
          this.PageRules()
        }
      } else {
        this.deviceSerialNo = ""
      }
    },
    // 不同页面特有的表单验证
    PageRules () {
      this.Rules.name = [
        {
          required: false,
          message: "请输入序列号",
          trigger: "blur"
        }
      ]
      this.Rules.RoomID = [
        {
          required: true,
          trigger: "change",
          message: "请选择教室"
        }
      ]
      if (this.number === 12) {
        this.Rules.name = [
          {
            required: true,
            message: "请输入序列号",
            trigger: "blur"
          }
        ]
      }
      if (this.number === 1) {
        this.Rules.name = [
          {
            required: true,
            message: "请输入序列号",
            trigger: "blur"
          },
          {
            validator: this.ValidateCtrlNo,
            trigger: "blur"
          }
        ]
        this.Rules.RoomID = [
          {
            required: true,
            trigger: "change",
            message: "请选择教室"
          },
          {
            validator: this.ValidateCtrlCls,
            trigger: "change"
          }
        ]
      }
    }
  }
}
</script>

